@import "mixin.scss";

* {
    box-sizing: border-box;
}

html {
    height: 100%;
}

body {
    font-family: "TaiwanPearl-Regular", "Noto Sans TC", sans-serif;
    margin: 0;
    width: 100%;
    height: 100%;
}

main, #root {
    width: 100%;
    height: 100%;
}

main {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.section_group {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1000vh;
    transition: transform 0.4s ease-in-out;
}

section {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #FFFFFF;
    z-index: 0;
    width: 100%;
    height: 10%;
    // top: 0;
    // left: 0;
    // transform: translateY(100%);
    // transition: transform 0.4s ease-in-out;

    // &.now_display {
    //     position: fixed;
    //     transform: translateY(0%);
    //     z-index: 1;
    // }

    // &.out {
    //     transform: translateY(-100%);
    // }
}

@keyframes hint {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(100%);
    }

    51% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}

.content-hint {
    position: fixed;
    bottom: 20px;
    left: 50%;
    width: 34px;
    height: 34px;
    border: 1px solid #919191;
    background-color: #FFFFFF;
    border-radius: 50%;
    transform: translateX(-50%);
    z-index: 10;
    overflow: hidden;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.5s ease-in-out;

    &.display {
        opacity: 1;
        pointer-events: auto;
        cursor: pointer;
    }

    @include rwd(pad) {
        width: 50px;
        height: 50px;
        bottom: 30px;
    }

    svg {
        display: block;
        width: 100%;
        height: 100%;
        animation: hint 2s ease-in-out infinite;
    }
}

.main {
    h1 {
        max-width: 184px;
        margin: 10px auto 0;
        // font-size: 14px;
        // letter-spacing: 0.1em;
        // color: #919191;
        // font-weight: normal;

        @include rwd(pad) {
            max-width: 290px;
        //     font-size: 20px;
        }
    }

    svg {
        display: block;
        width: 100%;
        height: auto;
    }

    .text {
        display: none;
    }

    .time {
        position: absolute;
        display: none;
    }

    .rocket {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: -70px auto -75px;
        width: 40px;
        transform-origin: center center;
        transform: rotate(90deg);

        &::after {
            position: absolute;
            display: block;
            content: "";
            right: 0;
            top: 0;
            width: 100%;
            height: var(--prog);
            background-color: rgba(#FFF, 1);
            mix-blend-mode: color;
        }

        &::before {
            position: absolute;
            display: block;
            content: "";
            right: 0;
            top: 0;
            width: 100%;
            height: var(--prog);
            background-color: rgba(#FFF, 0.5);
            mix-blend-mode: overlay;
        }
    
        svg {
            display: block;
            width: 100%;
            height: auto;
        }
    
        @include rwd(pad) {
            width: 66px;
            margin: -115px auto;
        }
    }
}

.month, .day {
    position: relative;
    width: 76px * 0.5;
    height: 50px * 0.5;
    margin: 0 auto;

    @include rwd(pad) {
        width: 76px;
        height: 50px;
    }

    svg {
        display: block;
        width: auto;
        height: 100%;
    }

    .number {
        width: 100%;
        height: 100%;
    }

    .mask {
        top: 0;
        left: 0;
        width: 0%;
        height: 100%;
        position: absolute;
        overflow: hidden;
    }
}

.month {
    margin-bottom: 5px;

    @include rwd(pad) {
        margin-bottom: 10px;
    }
}

.day {
    margin-top: 15px;

    @include rwd(pad) {
        margin-top: 25px;
    }
}

