.intro {
    .text_group {
        width: 100%;
        max-width: 260px;
        margin: 0 auto;

        @include rwd(pad) {
            max-width: 300px;
        }

        @include rwd(desktop) {
            max-width: 450px;
        }
    }

    .pic {
        width: 84px;

        @include rwd(pad) {
            width: 180px;
        }

        @include rwd(desktop) {
            width: 180px;
        }
    }

    svg {
        display: block;
        width: 100%;
        height: auto;
    }

    p {
        margin: 25px 0;

        @include rwd(pad) {
