        }
    }
}

.versus {
    position: relative;
    z-index: 1;
    background-color: #FFF;
    overflow: hidden;

    &.play {
        .background-left {
            transition: transform 0.5s 0.4s ease-in-out;
            transform: translate(-500px, -500px);
    
            @include rwd(desktop) {
                transform: translate(-1180px, -680px);
            }
        }

        .background-right {
            transition: transform 0.5s 0.4s ease-in-out;
            transform: translate(520px, 0px);
    
            @include rwd(desktop) {
                transform: translate(1180px, -184px);
            }
        }

        .vs {
            opacity: 1;
            svg {
                transform: translate(-50%, -50%) scale(1);
                transition: transform 0.5s 0.8s ease-in-out;
            }
        }

        .cat {
            transition: transform 1s ease-in-out;
            transform: translate(calc(-50% - 160px), calc(-50% - 180px)) rotate(20deg);
    
            @include rwd(pad) {
                transform: translate(calc(-50% - 220px), calc(-50% - 220px)) rotate(20deg);
            }
    
            @include rwd(desktop) {
                transform: translate(calc(-50% - 600px), calc(-50% - 80px)) rotate(20deg);
            }
        }

        .dinosaur {
            transition: transform 1s ease-in-out;
            transform: translate(calc(-50% + 190px), calc(-50% + 240px)) rotate(20deg);
    
            @include rwd(pad) {
                transform: translate(calc(-50% + 240px), calc(-50% + 300px)) rotate(20deg);
            }
    
            @include rwd(desktop) {
                transform: translate(calc(-50% + 680px), calc(-50% + 236px)) rotate(20deg);
            }
        }
    }

    .background-left {
        position: absolute;
        width: 540px;
        top: 50%;
        left: 50%;
        transform: translate(-200vw, -500px);

        @include rwd(desktop) {
            width: 1020px;
            transform: translate(-200vw, -680px);
        }
    }

    .background-right {
        position: absolute;
        width: 540px;
        top: 50%;
        right: 50%;
        transform: translate(200vw, 0px);

        @include rwd(desktop) {
            width: 1020px;
            transform: translate(200vw, -184px);
        }
    }

    svg {
        display: block;
        width: 100%;
        height: auto;
    }

    .vs {
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        overflow: hidden;
        opacity: 0;

        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 260px;
            transform-origin: center center;
            transform: translate(-50%, -50%) scale(300);

            @include rwd(desktop) {
                width: 580px;
            }
        }
    }

    .cat {
        position: absolute;
        z-index: 1;
        width: 300px;
        top: 50%;
        left: 50%;
        transform: translate(calc(-50% + 100vw), calc(-50% - 180px)) rotate(20deg);

        @include rwd(pad) {
            width: 400px;
            transform: translate(calc(-50% + 100vw), calc(-50% - 220px)) rotate(20deg);
        }

        @include rwd(desktop) {
            width: 600px;
            transform: translate(calc(-50% + 100vw), calc(-50% - 80px)) rotate(20deg);
        }

        svg {
            position: relative;
            z-index: 1;
        }

        .rocket {
            position: absolute;
            z-index: 0;
            left: 40%;
            top: -60%;
            width: 21%;
            transform: rotate(60deg);
        }
    }

    .dinosaur {
        position: absolute;
        z-index: 1;
        width: 300px;
        top: 50%;
        left: 50%;
        transform: translate(calc(-50% - 100vw), calc(-50% + 240px)) rotate(20deg);

        @include rwd(pad) {
            width: 400px;
            transform: translate(calc(-50% - 100vw), calc(-50% + 300px)) rotate(20deg);
        }

        @include rwd(desktop) {
            width: 600px;
            transform: translate(calc(-50% - 100vw), calc(-50% + 236px)) rotate(20deg);
        }

        svg {
            position: relative;
            z-index: 1;
        }

        .castle {
            position: absolute;
            bottom: -12%;
            left: -26%;
            width: 45%;
            transform: rotate(-25deg);
        }
    }
}

.invitation {
    position: relative;
    z-index: 0;

    .background-left {
        position: absolute;
        display: none;
        width: 1020px;
        top: 360px;
        left: 50%;
        transform: translate(-1200px, 0);

        @include rwd(desktop) {
            display: block;
        }
    }

    .background-right {
        position: absolute;
        display: none;
        width: 1020px;
        top: -658px;
        right: 50%;
        transform: translate(1180px, 0);

        @include rwd(desktop) {
            display: block;
        }
    }

    .content {
        position: relative;
        width: auto;
        height: 90vh;
        margin: 0 auto;

        // @include rwd(pad) {
        //     width: 780px;
        //     height: auto;
        // }

        @include rwd(desktop) {
            width: auto;
            height: 135vh;
            margin-top: -32vh;
        }

        svg {
            width: auto;
            height: 100%;
        }
    }

    .castle {
        position: absolute;
        width: 100px;
        top: 50%;
        left: 50%;
        transform: translate(120px, -175px) rotate(-6deg);
        
        @include rwd(pad) {
            transform: translate(200px, -200px) rotate(-6deg) scale(1.5);
        }

        @include rwd(desktop) {
            display: none;
        }
    }

    .controller {
        position: absolute;
        width: 70px;
        top: 50%;
        left: 50%;
        transform: translate(-180px, -180px);
        
        @include rwd(pad) {
            transform: translate(-280px, -280px) scale(1.5);
        }

        @include rwd(desktop) {
            display: none;
        }
    }

    .rocket {
        position: absolute;
        width: 40px;
        top: 50%;
        left: 50%;
        transform: translate(-200px, 100px) rotate(33deg);
        
        @include rwd(pad) {
            transform: translate(-290px, 180px) rotate(33deg) scale(1.5);
        }

        @include rwd(desktop) {
            display: none;
        }
    }

    svg {
        display: block;
        width: 100%;
        height: auto;
    }

    @include rwd(desktop) {
        .hide {
            display: none;
        }
    }
}

.transportation {
    padding: 0 20px;
    z-index: 1;

    .text_group {
        position: relative;
        margin: 0 auto;
        max-width: 440px;
        z-index: 1;

        @include rwd(pad) {
            max-width: 600px;
        }
    }

    h3 {
        margin-top: 40px;
        margin-bottom: 10px;
        font-size: 16px;
        color: #919191;
        text-align: center;
        font-weight: bold;

        &:first-child {
            margin-top: 0;
        }

        @include rwd(pad) {
            margin-top: 60px;
            margin-bottom: 30px;
            font-size: 28px;
        }
    }

    ul {
        list-style: none;
        padding: 0 0 0 1em;

        li {
            text-indent: -1.3em;
            font-weight: bold;
            font-size: 14px;
            color: #604C3F;
            line-height: 2em;

            @include rwd(pad) {
                font-size: 20px;
            }
        }
    }

    ol {
        margin: 0 auto;
        padding: 0;
        width: 310px;
        display: flex;
        flex-wrap: wrap;

        @include rwd(pad) {
            width: 440px;
        }

        li {
            display: block;
            width: 50%;
            font-weight: bold;
            font-size: 14px;
            color: #604C3F;
            line-height: 2em;

            @include rwd(pad) {
                font-size: 20px;
            }
        }
    }

    p {
        margin-top: 0;
        text-align: center;
        font-weight: bold;
        font-size: 14px;
        color: #7FB6C4;
        line-height: 2em;

        @include rwd(pad) {
            font-size: 20px;
        }
    }
}

