}

.lyrics {
    position: relative;
    z-index: 2;
    background-color: #FFF;

    .text_group {
        width: 100%;
        max-width: 230px;
        margin: 0 auto;

        @include rwd(pad) {
            max-width: 300px;
        }

        @include rwd(desktop) {
            padding: 0 50px;
            max-width: 900px;
        }
    }

    .pic {
        width: 84px;

        @include rwd(pad) {
            width: 180px;
        }

        @include rwd(desktop) {
            width: 180px;
            display: none;

            &:first-child {
                display: block;
                margin: 0 auto;
            }
        }
    }

    svg {
        display: block;
        width: 100%;
        height: auto;
    }

    .lyric_text {
        margin: 25px 0;

        @include rwd(pad) {
            margin: 40px 0;
        }
    }

    .dot {
        margin: 22px 0 22px 5px;
        display: block;
        width: 2px;
        height: 2px;
        background-color: #919191;
        border-radius: 50%;

        @include rwd(pad) {
            margin: 30px 0 30px 5px;
            width: 4px;
            height: 4px;
        }

        @include rwd(desktop) {
            display: none;
        }
    }

    .lyric_group {
        @include rwd(desktop) {
            display: flex;
            justify-content: space-between;
            margin: 70px 0;

            svg {
                width: 50%;
                max-width: 350px;
            }
        }
    }

    .quote {
        @include rwd(desktop) {
            margin: 0 auto;
            max-width: 340px;
        }
    }
}

.photos {
    padding: 0 20px;

    @include rwd(desktop) {
        padding: 0;
    }

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    .photo_group {
        @include rwd(desktop) {
            display: flex;
        }
    }

    .photo_row {
        display: flex;
        max-width: 500px;

        @include rwd(desktop) {
            width: 50%;
            max-width: none;
        }
    }

    .photo_item {
        width: 50%;
        padding: 4px;

        &.first {
            transform: translateY(-16%);

            @include rwd(desktop) {
                transform: translateY(0);
            }
        }

        &.second {
            transform: translateY(19%);

            @include rwd(desktop) {
                transform: translateY(0);
            }
        }
    }

    .pic {
        display: none;

        @include rwd(desktop) {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50%;

            svg {
                display: block;
                width: 172px;
                height: auto;
            }

            + .photo_item {
                display: none;
            }
        }
    }
}

